.MoviesCardList {
    background-color: #202020;
    margin: 0;
    width: 100%;
}
.MoviesCardList__container {
    max-width: 1140px;
    margin: 0 auto;
    display: grid;
    grid-auto-rows: auto;
    grid-template-columns: repeat(4, 270px);
    row-gap: 38px;
    column-gap: 20px;
    padding: 80px 0 240px 0;
}
.MoviesCardList__container-low {
    margin-bottom: 240px;
}
@media screen and (max-width: 800px) {
    .MoviesCardList__container-low {
        margin-bottom: 150px;
    }
}